html,
body, #root {
  height: 100%;
}

#root {
  font-size: 1rem;
  font-weight: bold;
  text-align: center;
  touch-action: none;

  background: red;
  display: flex;
  flex-direction: column;
}

.header {
  background-color: #282c34;
  color: white;
  padding: 10px;
}

.content {
  background: lightblue;
  flex-grow: 1;
  display: flex;
  justify-content: center;
}
